.button {
    display: block;
    height: 30px;
    border-style: solid;
    border-width: 0px 0px 3px;
    box-shadow: 0 -1px 0 rgba(255, 255, 255, 0.1) inset;
    color: #ffffff;
    border-radius: 6px;
    cursor: pointer;
    /*display: inline-block;*/
    font-style: normal;
    text-align: center;
    text-overflow: ellipsis;
    transition: all 200ms ease-in-out 0s;
    white-space: nowrap;
    font-weight: 700;
    font-size: 14px;
    border-color: #3ac162;
    background-color: #5fcf80;
}

.button:hover,
.button:focus,
.button:active {
    background-color: #4bc970;
    border-color: #3ac162;
}
